'use strict';(function(){'use strict';angular.module('app',['ui.router','duScroll']).config(routes).config(config).run(run);function config($locationProvider,$logProvider){$locationProvider.html5Mode(true);$logProvider.debugEnabled(true)}function routes($stateProvider,$urlRouterProvider){$stateProvider.state('home',{url:'/',component:'home'});$urlRouterProvider.otherwise('/')}function run($log,$http,$rootScope){$rootScope.keys=Object.keys;$http.get('/assets/data/resume.json').then(function(data){return $rootScope.resume=data.data}).catch(function(){return $log.error('Failed to load resume.')})}})();
'use strict';(function(){'use strict';angular.module('app').directive('home',home);function home(){return{controller:'HomeController',controllerAs:'hc',templateUrl:'app/home/home.html'}}})();
'use strict';(function(){'use strict';angular.module('app').controller('HomeController',HomeController);function HomeController($log,$document,$location){var vm=this;vm.scrollTo=scrollTo;initialize();function initialize(){$('.button-collapse').sideNav()}function scrollTo(id){$document.scrollToElementAnimated(angular.element(document.getElementById(id)))}}})();
angular.module('app').run(['$templateCache', function($templateCache) {$templateCache.put('app/home/home.html','<div class=home><header><nav><ul id=slide-out class="side-nav fixed"><li><div class=userView><img class=circle src={{resume.profile}}><div class=iconized-info-panel><div class="white-text iconized-info"><span class=icon><i class=material-icons>person</i></span><b>{{resume.name}}</b></div><div class="white-text iconized-info"><span class=icon><i class=material-icons>email</i></span>{{resume.email}}</div><div class="white-text iconized-info"><span class=icon><i class=material-icons>today</i></span>{{resume.dateOfBirth}}</div><div class="white-text iconized-info"><span class=icon><i class=material-icons>my_location</i></span>{{resume.location}}</div></div></div></li><li><a class="waves-effect waves-secondary" ng-click="hc.scrollTo(\'summary\')"><i class=material-icons>contact_mail</i>Summary</a></li><li><a class="waves-effect waves-secondary" ng-click="hc.scrollTo(\'education\')"><i class=material-icons>school</i>Education</a></li><li><a class="waves-effect waves-secondary" ng-click="hc.scrollTo(\'publications\')"><i class=material-icons>library_books</i>Publications</a></li><li><a class="waves-effect waves-secondary" ng-click="hc.scrollTo(\'cv\')"><i class=material-icons>insert_drive_file</i>Curriculum Vitae</a></li><li><div class=divider></div></li><li><a class=subheader>Social</a></li><!-- Had to use font awesome, since MaterialIcons do not have social icons--><li><a class="waves-effect waves-secondary" ng-href={{resume.twitter}}><i class="fa fa-lg fa-twitter"></i>Twitter</a></li><li><a class="waves-effect waves-secondary" ng-href={{resume.github}}><i class="fa fa-lg fa-github"></i>Github</a></li><li><a class="waves-effect waves-secondary" ng-href={{resume.linkedin}}><i class="fa fa-lg fa-linkedin"></i>LinkedIn</a></li><li><a class="waves-effect waves-secondary" ng-href={{resume.googleScholar}}><i class="ai ai-lg ai-google-scholar"></i>Google Scholar</a></li><li><a class="waves-effect waves-secondary" ng-href={{resume.researchGate}}><i class="ai ai-lg ai-researchgate"></i>Research Gate</a></li></ul><a href=# data-activates=slide-out class=button-collapse><i class=material-icons>menu</i></a></nav></header><main><div class=container><div id=summary class="row section scrollspy"><div class="col s12"><h2>Summary</h2><div class=card-panel><p class=caption>{{ resume.summary }}</p></div></div></div><div id=education class="row section scrollspy"><div class="col s12"><h2>Education</h2><div class=timeline><div class=timeline-event ng-repeat="entry in resume.education"><div class="card timeline-content"><div class="card-image waves-effect waves-block waves-light"><img class=activator src={{entry.image}}></div><div class=card-content><span class="card-title activator grey-text text-darken-4">{{entry.title}} <i class="material-icons right">more_vert</i></span><p>{{entry.institution}}</p><p><b>{{entry.startDate}} &mdash; {{entry.endDate}}</b></p></div><div class=card-reveal><span class="card-title grey-text text-darken-4">{{entry.title}} <i class="material-icons right">close</i></span><div class=iconized-info-panel><div class=iconized-info><span class=icon><i class=material-icons>account_balance</i></span>{{entry.institution}}</div><div class=iconized-info><span class=icon><i class=material-icons>place</i></span>{{entry.location}}</div><div class=iconized-info><span class=icon><i class=material-icons>event</i></span>{{entry.startDate}} &mdash; {{entry.endDate}}</div><p>{{entry.description}}</p></div></div></div><div class="timeline-badge white-text"><i class=material-icons>school</i></div></div></div></div></div><div id=publications class="row section scrollspy"><h2>Publications</h2><div class=card-panel><div class=publications><h4>Conferences</h4><div class=publication ng-repeat="entry in resume.publications.conferences"><span class=icon><i class=material-icons>description</i></span><div class=info><div><a ng-if=entry.doi href="{{ entry.doi }}"><b>{{entry.title}}</b></a><b ng-if=!entry.doi>{{entry.title}}</b></div><div>{{entry.authors}}</div><div>{{entry.conference}}, {{entry.location}}, {{entry.year}}.</div></div></div><h4>Journals</h4><div class=publication ng-repeat="entry in resume.publications.journals"><span class=icon><i class=material-icons>book</i></span><div class=info><div><a ng-if=entry.doi href="{{ entry.doi }}"><b>{{entry.title}}</b></a><b ng-if=!entry.doi>{{entry.title}}</b></div><div>{{entry.authors}}</div><div>{{entry.journal}}, Volume {{entry.volume}}, pages {{entry.pages}}, {{entry.year}}</div></div></div><h4>Workshops, Posters and Others</h4><div class=publication ng-repeat="entry in resume.publications.others"><span class=icon><i class=material-icons>assignment</i></span><div class=info><div class=flex><div><a ng-if=entry.doi href="{{ entry.doi }}"><b>{{entry.title}}</b></a><b ng-if=!entry.doi>{{entry.title}}</b></div><span ng-if=entry.award><span class=icon><i class="material-icons small">stars</i></span><span>{{ entry.award }}</span></span></div><div>{{entry.authors}}</div><div>{{entry.conference}}, {{entry.location}}, {{entry.year}}.</div></div></div></div></div></div><div id=cv class="row section scrollspy"><div class=curriculum-vitae><h2>Curriculum Vitae</h2><a target=_self href=/assets/file/cv.pdf download=asjacobs-cv.pdf class="download waves-effect waves-secondary btn"><i class="material-icons left">file_download</i>Download</a></div></div><div></div></div></main></div>');}]);